













import Vue from 'vue';

import Task from '@/models/Task';

export default Vue.extend({
    props: {
        task: {
            type: Object as () => Task,
            required: true,
        },
    },
    methods: {
        toggle() {
            this.$ui.wrapAsyncOperation(
                this.$workspaces.toggleTask(this.task),
                'Toggling task...',
            );
        },
    },
});
